* {
	box-sizing: border-box;
}

.column {
	float: left;
	width: 25%;
	padding: 0 10px;
}

.chip {
	display: inline-block;
	height: 160px;
	font-size: 16px;
	line-height: 50px;
	width: 230px;
	margin: 10px;
	cursor: pointer;
	text-align: center;
	margin-top: 40px;
}

.chip:hover {
	opacity: 0.6;
}

.chip h5 {
	margin-top: -25px;
}

.chip h6 {
	margin-top: -50px;
	font-weight: normal;
}

.chip-img {
	justify-content: center;
	margin-top: -30px;
	height: 110px;
	width: 110px;
	border-radius: 50%;
	border: 2px solid #69d0a3;
}

.chip-img-border-man {
	border: 2px solid #475669 !important;
}

.chip-flag {
	margin-top: 40px;
	height: 32px;
	width: 32px;
	margin-left: -25px;
	vertical-align: top;
	border-radius: 50%;
	border: none;
}

.load-more-btn {
	height: 110px;
	font-size: 16px;
	line-height: 40px;
	width: 110px;
	margin: 10px;
	cursor: pointer;
	text-align: center;
	margin-top: -30px;
	border-radius: 50%;
	background: #4495b3;
	color: #fff;
	border: none;
}

.load-more-btn:active {
	outline: none;
}

@media screen and (max-width: 683px) {
	.chip {
		width: 120px;
		margin-left: 20px;
		font-size: x-small;
		min-width: 120px;
	}

	.chip h5 {
		margin-top: -30px;
	}

	.chip h6 {
		margin-top: -50px;
		font-weight: normal;
		font-size: xx-small;
	}

	.chip-img {
		margin-top: -30px;
		height: 90px;
		width: 90px;
	}

	.chip-flag {
		margin-top: 40px;
		height: 18px;
		width: 18px;
		margin-left: -25px;
	}

	.load-more-btn {
		height: 90px;
		font-size: 12px;
		width: 90px;
	}
}
