* {
	box-sizing: border-box;
}

/* Users Grid Styles */
.users-table {
	width: 50%;
	margin: auto;
	position: relative;
	padding-top: 0px;
	margin-top: 20px;
	display: grid;
	flex-direction: column;
	border: 1px solid #9e9e9e;
	box-shadow: 0px 1px 4px #9e9e9e;
	border-radius: 30px;
	padding-right: 10px;
	padding-left: 10px;
	background: transparent;
	margin-bottom: 25px;
}

.users-table__row {
	display: grid;
	grid-template-columns: 0.1fr 2fr 0.1fr 1fr;
	border-bottom: 0.12rem solid #c5c5c5;
	padding: 1rem 2rem;
	background-color: honeydew;
	border-radius: 30px;
	margin-top: 1px;
	cursor: pointer;
}

.users-table__row:hover {
	opacity: 0.4;
}

.users-table__row__div {
	margin: auto;
	margin-left: 10px;
}
.users-table__row:last-child {
	border-bottom: none;
}
.users-table__row:nth-child(odd) {
	background-color: rgb(241, 241, 241);
}

.user-image {
	border-radius: 50%;
	width: 60px;
	border: 2px solid #69d0a3;
}

.user-image:hover {
	opacity: 0.7;
}

.user-image-border-man {
	border: 2px solid #475669 !important;
}

.userName {
	font-weight: bold;
}

.chip-flag-table {
	height: 32px;
	width: 32px;
	border: none;
}

@media screen and (max-width: 450px) {
	.users-table {
		width: 100%;
		margin-left: 3px !important;
		margin-right: 5px;
		font-size: xx-small;
		padding: 0px;
		border-radius: 0%;
		border: none;
		box-shadow: none;
	}

	.users-table__row {
		padding: 0;
		border-radius: 10px;
		margin-top: 3px;
		padding: 0px;
		font-size: xx-small;
		width: auto;
		min-width: 240px;
		/* margin-right: 10px !important; */
	}

	.user-image {
		width: 35px;
	}

	.chip-flag-table {
		height: 16px;
		width: 16px;
	}

	.pagination {
		padding-top: 2px;
		padding-bottom: 40px !important;
		font-size: xx-small;
	}

	.pagination a {
		padding: 0px;
	}
}

@media screen and (max-width: 900px) {
	.users-table {
		width: 95%;
		margin-left: 20px;
		font-size: small;
	}

	.user-image {
		width: 50px;
	}

	.chip-flag-table {
		height: 24px;
		width: 24px;
	}
}
/* //////////////////////////////////////////// */

/* pagination Styles */
.pagination {
	justify-content: center;
	text-align: center;
	display: flex;
	padding-top: 5px;
	padding-bottom: 5px;
}

.pagination button {
	color: black;
	text-align: center;
	padding: 8px 16px;
	text-decoration: none;
	transition: background-color 0.3s;
	outline: none;
	border: none;
}

.pagination button.active {
	background-color: #4495b3;
	color: white;
	border-radius: 15%;
	outline: none;
}

.pagination button:hover:not(.active) {
	background-color: rgb(206, 205, 205);
}
/* //////////////////////////////////////////// */
